import { Col, Pagination, Row, Switch, Table } from "antd";
import { useCallback, useEffect, useState } from "react";
import { TableColumn } from "../../models/baseModel";
import { MyFormItem } from "./Input";
import { Box } from "./MyBox";
import { MyButton } from "./MyButton";
import { OrderByModel, PageInfoModel } from "./TableModel";
import './UIStyle.css';
// pageInfo={pageInfo}
// setPageInfo={setPageInfo}
// column={column}
// dataSource={data}
// loading={isLoading}
// loadData={loadData}

// {title:"Title",dataIndex:"dataIndex",key:"key",render=()=>{})}
export default function TableComponent(props: any) {
    const { column, setOrderBy, setPageInfo, loading, dataSource, scroll, rowClassName, loadData } = props;

    const pageInfo: PageInfoModel = props.pageInfo;
    const orderBy: OrderByModel = props.orderBy;

    const [columnProps, setColumnProps] = useState([]);
    const [timer, setTimer] = useState(100);

    useEffect(() => {
        const interval = setInterval(() => {
            setTimer(timer - 1);
            if (timer === 1) {
                setTimer(100);
                loadData();
            }
        }, 1000);

        return () => clearInterval(interval);
    }, [timer, setTimer, loadData]);

    const renderColumn = useCallback(() => {
        if (column === undefined) {
            return;
        }

        setColumnProps(column.map((data: TableColumn, index: number) => {
            return {
                key: data.key,
                className: data.className,
                width: data.width,
                title: data.title,
                dataIndex: data.dataIndex,
                render: data.render,
            };
        }));
    }, [column]);

    useEffect(() => {
        renderColumn();
    }, [renderColumn]);

    function handleChangeTypeOrder(data: any) {
        orderBy.setDesc(data, setOrderBy);
    }

    function handleChangeValue(data: any) {
        orderBy.setColumn(data, setOrderBy);
    }

    function handleChangePage(page: number, pageSize: number) {
        pageInfo.handleChangePage(page, pageSize, setPageInfo);
    }

    function handleClickRefreshData() {
        loadData();
    }

    return (
        <div className="my_table">
            <Box>
                <Row gutter={16}>
                    <Col span={6}>
                        <MyButton
                            type="secondary"
                            value={`Refresh data in ${timer}`}
                            onClick={handleClickRefreshData} />
                    </Col>
                    <Col span={12}>
                        <MyFormItem.Select
                            value={orderBy?.column}
                            onChange={handleChangeValue}
                            options={column} />
                    </Col>
                    <Col span={6}>
                        <Switch
                            disabled={orderBy?.column === ''}
                            checked={orderBy?.esc}
                            onChange={handleChangeTypeOrder}
                            checkedChildren="Tăng dần"
                            unCheckedChildren="Giảm dần" />
                    </Col>
                    <Col span={12}>
                        <Pagination
                            style={{ display: pageInfo.showPagination ? '' : 'none' }}
                            current={pageInfo.pageNumber}
                            disabled={loading}
                            onChange={handleChangePage}
                            pageSize={pageInfo.pageSize}
                            total={pageInfo.total} />
                    </Col>

                </Row>
            </Box>

            <Table
                scroll={{ y: scroll }}
                rowClassName={rowClassName}
                style={{ "paddingTop": "10px" }}
                bordered
                loading={loading}
                pagination={false}
                dataSource={dataSource}
                size="small"
                columns={columnProps}>
                {/* {renderColumn()} */}
            </Table>
        </div>
    );
}