import { Col, Row } from "antd";
import { useCallback, useEffect, useState } from "react";
import { useLocation, useParams } from "react-router-dom";
import { PaginationRequest } from "../../../../../models/baseModel";
import { DocumentService } from "../../../../../services/taskService";
import { Box, MyBox } from "../../../../core/MyBox";
import { MyButton } from "../../../../core/MyButton";
import { OrderByModel } from "../../../../core/TableModel";
import "../../task.css";
import ApiDocComponent from "./ApiDocComponent";
import ModifyComponent from "./ModifyComponent";

export default function IndexComponent() {
    const search = useLocation().search;
    const folderKey = new URLSearchParams(search).get('folder');
    const [folderChoose, setFolderChoose] = useState(folderKey);

    const projectKey = useParams().key;
    const [data, setData] = useState([]);
    const [visible, setVisible] = useState(false);

    const [visibleEdit, setVisibleEdit] = useState(false);
    const [keyEdit, setKeyEdit] = useState<string | undefined>(undefined);

    const [isLoading, setIsLoading] = useState(false);

    const loadData = useCallback(() => {
        setIsLoading(true);
        let pagination = new PaginationRequest(1, 100000, new OrderByModel());
        DocumentService.GetList(projectKey, pagination)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    setData(rs.data.pagedData);
                }
            })
            .finally(() => setIsLoading(false));
    }, [projectKey]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function renderData() {
        return data.map((data: any, index: number) => {
            return (
                <li key={index} className="item-folder">
                    <MyButton
                        className={data.key === folderChoose ? "folder-selected" : ""}
                        onDoubleClick={() => {
                            handleDoubleClickDetail(data.key)
                        }}
                        onClick={() => {
                            handleClickDetailFolder(data.key)
                        }}
                        value={data.name} />
                </li>
            );
        })
    }

    function handleClickDetailFolder(key: string) {
        setFolderChoose(key);
    }

    function handleDoubleClickDetail(key: string) {
        setVisibleEdit(true);
        setKeyEdit(key);
    }
    function handleClickAddFolder() {
        setVisible(true);
    }

    return (
        <MyBox
            title="Document API"
            isLoading={isLoading}>
            <Row gutter={6}>
                <Col span={4}>
                    <Box>
                        <MyButton type="primary" onClick={handleClickAddFolder} value="Thêm thư mục" />
                        <ul>
                            {renderData()}
                        </ul>
                    </Box>
                </Col>
                <Col span={20}>
                    <ApiDocComponent
                        folderKey={folderChoose}
                        projectKey={projectKey}
                        isLoading={isLoading}
                        setIsLoading={setIsLoading} />
                </Col>
            </Row>

            <ModifyComponent
                visible={visible}
                setVisible={setVisible}
                projectKey={projectKey}
                loadData={loadData}
                isLoading={isLoading}
                setIsLoading={setIsLoading} />

            <ModifyComponent
                visible={visibleEdit}
                setVisible={setVisibleEdit}
                keyEdit={keyEdit}
                setKeyEdit={setKeyEdit}
                projectKey={projectKey}
                loadData={loadData}
                isLoading={isLoading}
                setIsLoading={setIsLoading} />
        </MyBox>
    );
}