import moment from "moment";

export function UIFormatDateVNComponent(props: any) {
    const { value } = props;
    let dateStr = value !== undefined && value !== '' && value !== null ?
        moment(value).format("DD/MM/YYYY") : "";
    return <span>{dateStr}</span>
}

export function UIFormatDateTimeVNComponent(props: any) {
    const { value } = props;
    let dateStr = value !== undefined && value !== '' && value !== null ?
        moment(value).format("DD/MM/YYYY HH:mm:ss") : "";
    return <span>{dateStr}</span>
}