import { useCallback, useEffect, useState } from "react";
import { Fn } from "../../../../../helper/function";
import { TableColumn } from "../../../../../models/baseModel";
import { ApiDocService } from "../../../../../services/taskService";
import { Box } from "../../../../core/MyBox";
import { MyButton } from "../../../../core/MyButton";
import { OrderByModel, PageInfoModel } from "../../../../core/TableModel";
import TableComponent from "../../../../core/TableNewComponent";
import "../../task.css";
import { UIFormatDateTimeVNComponent } from "../../../../core/UI_FormatComponent";
import { Link } from "react-router-dom";
import { TextWrap } from "../../../../core/Text";
import { Tag } from "antd";

export default function ApiDocComponent(props: any) {
    const { isLoading, setIsLoading } = props;
    const [data, setData] = useState([]);

    const [pageInfo, setPageInfo] = useState<PageInfoModel>(new PageInfoModel());
    const [orderBy, setOrderBy] = useState<OrderByModel>(new OrderByModel());
    const column: any = [
        new TableColumn({
            title: "STT", key: "stt", render: (data: any, row: any, index: any) =>
                Fn.RenderSTT(data, row, index, pageInfo)
        }),
        new TableColumn({
            title: "Method", key: "method", render: (data: number) => {
                var method = "";
                var color = "";
                switch (data) {
                    case 1:
                        method = "Get";
                        color = "green";
                        break;
                    case 2:
                        method = "Post";
                        color = "blue";
                        break;
                    case 3:
                        method = "Put";
                        color = "purple";
                        break;
                    case 4:
                        method = "Delete";
                        color = "red";
                        break;
                    default:
                        break;
                }
                return <Tag color={color}>{method}</Tag>
            }
        }),
        new TableColumn({
            title: "Tên", key: "name", render: (data: string, row: any) =>
                <Link to={`/tools/tasks/projects/${props.projectKey}/codeManage/document/${props.folderKey}/apis/${row.key}`}>
                    {data}
                </Link>
        }),
        new TableColumn({
            title: "Url", key: "url", render: (data: string) =>
                <TextWrap>{data}</TextWrap>
        }),
        new TableColumn({
            title: "Ngày tạo", key: "createdAt", render: (data: string) =>
                <UIFormatDateTimeVNComponent value={data} />
        }),
        new TableColumn({
            title: "Ngày cập nhật cuối", key: "updatedAt", render: (data: string) =>
                <UIFormatDateTimeVNComponent value={data} />
        })
    ];

    const loadData = useCallback(() => {
        if (props.folderKey !== undefined && props.folderKey !== null) {
            setIsLoading(true);
            ApiDocService.GetList(props.projectKey, props.folderKey)
                .then((rs: any) => {
                    if (rs.isSuccess) {
                        setData(rs.data.pagedData);
                    }
                })
                .finally(() => setIsLoading(false));
        }
    }, [props.projectKey, props.folderKey, setIsLoading]);

    useEffect(() => {
        loadData();
    }, [loadData])

    return (
        <Box>
            <Link to={`/tools/tasks/projects/${props.projectKey}/codeManage/document/${props.folderKey}/apis/add`}>
                <MyButton
                    type="primary"
                    value="Thêm API mới" />
            </Link>

            <TableComponent
                pageInfo={pageInfo}
                setPageInfo={setPageInfo}
                orderBy={orderBy}
                setOrderBy={setOrderBy}
                column={column}
                dataSource={data}
                loading={isLoading}
                loadData={loadData} />
        </Box>
    );
}