export class PageInfoModel {
    pageNumber: number = 1;
    pageSize: number = 10;
    total: number = 0;
    showPagination: boolean = true;

    constructor(data?: any) {
        if (data === undefined) return;
        const pageInfo = data.data.pageInfo;
        this.total = pageInfo.totalCount;
        this.pageNumber = pageInfo.currentPage;
        this.pageSize = pageInfo.pageSize;
    }

    handleChangePage(pageNumber: number, pageSize: number, setPageInfo: any) {
        this.pageNumber = pageNumber;
        this.pageSize = pageSize;

        setPageInfo(new PageInfoModel({
            data: {
                pageInfo: {
                    totalCount: this.total,
                    currentPage: this.pageNumber,
                    pageSize: this.pageSize
                }
            }
        }));
    }

    handleChangeOrderBy(orderBy: string, typeOrder: boolean, setPageInfo: any) {
        setPageInfo(new PageInfoModel({
            data: {
                pageInfo: {
                    totalCount: this.total,
                    currentPage: this.pageNumber,
                    pageSize: this.pageSize
                }
            }
        }));
    }

    setPageSize(pageSize: number, setPageInfo: any) {
        setPageInfo(new PageInfoModel({
            data: {
                pageInfo: {
                    totalCount: this.total,
                    currentPage: this.pageNumber,
                    pageSize: pageSize
                }
            }
        }));
    }
}


export class OrderByModel {
    column: string | undefined = '';
    esc: boolean | undefined = false;
    constructor(column?: string, desc?: boolean) {
        if (column !== undefined) {
            this.column = column;
            this.esc = desc;
        }
    }

    setColumn(column: string, setOrderBy: any) {
        setOrderBy(new OrderByModel(column, this.esc))
    }

    setDesc(desc: boolean, setOrderBy: any) {
        setOrderBy(new OrderByModel(this.column, desc))
    }

    genQueryOrderBy() {
        if (this.column === '') {
            return '';
        }

        if (!this.esc) {
            return this.column + " desc";
        }
        return this.column;
    }
}