import { OrderByModel } from "../components/core/TableModel";

export class ResponseDataModel<T> {
    data: T | null;
    message: string | undefined;
    statusCode: number | undefined;

    constructor(response: any) {
        let data = response.data;
        this.data = data.data;
        this.message = data.message;
        this.statusCode = data.statusCode;
    }
}

export class TableColumn {
    title: string
    dataIndex: string
    key: string
    render: any
    className: string | undefined
    width: number | undefined

    label: string | undefined
    value: string | undefined
    disabled: boolean | undefined = false;

    constructor(data?: any) {
        this.title = data.title;
        this.key = data.key;

        if(data.dataIndex === undefined) {
            this.dataIndex = data.key;
        } else {
            this.dataIndex = data.dataIndex;
        }

        this.render = data.render;
        this.className = data.className;
        this.width = data.width;
        this.label = data.title;
        this.value = data.key;

        if(this.key === "stt" || this.key === "action") {
            this.disabled = true;
        }

        if (data.disabled !== undefined) {
            this.disabled = data.disabled;
        }
    }
}

export class PaginationRequest {
    pageNumber: number = 1;
    pageSize: number = 10;
    orderBy: OrderByModel

    constructor(pageNumber: number, pageSize: number, orderBy: OrderByModel) {
        this.pageNumber = pageNumber;
        this.pageSize = pageSize;
        this.orderBy = orderBy;
    }

    generateQueryString(): string {
        // console.log(this);
        if(this === undefined) return "";
        
        return `pageNumber=${this.pageNumber}&pageSize=${this.pageSize}&orderBy=${this.orderBy.genQueryOrderBy()}`;
    }
}