import { Drawer, Form } from "antd";
import { useCallback, useEffect, useState } from "react";
import Swal from "sweetalert2";
import { Fn } from "../../../../../helper/function";
import { DocumentService } from "../../../../../services/taskService";
import { MyFormItem } from "../../../../core/Input";
import { MyButton } from "../../../../core/MyButton";
import HeaderComponent from "./HeaderComponent";

export default function ModifyComponent(props: any) {
    const [item, setItem] = useState([]);
    const [name, setName] = useState('');

    const { projectKey, keyEdit, setKeyEdit, visible, setVisible, isLoading, setIsLoading, loadData } = props;

    const loadDataDetail = useCallback(() => {
        if (keyEdit !== undefined) {
            setIsLoading(true);
            DocumentService.GetDetail(projectKey, keyEdit)
                .then((rs: any) => {
                    if (rs.isSuccess) {
                        setName(rs.data.name);
                        setItem(rs.data.headers);
                    }
                })
                .finally(() => setIsLoading(false));
        }
    }, [projectKey, keyEdit, setIsLoading]);

    useEffect(() => {
        loadDataDetail();
    }, [loadDataDetail])

    function onClose() {
        setVisible(false);
        if (setKeyEdit !== undefined) {
            setKeyEdit(undefined);
        }
    }

    function filterArray() {
        var t = item;

        t = t.filter((obj: any) => {
            return (obj !== undefined && obj.key !== '');
        })
        return t;
    }

    function handleSubmitForm() {
        let headers = filterArray();
        let body = {
            name: name,
            headers: headers
        }

        setIsLoading(true);
        if (keyEdit !== undefined) {
            edit(body);
        } else {
            add(body);
        }
    }

    function add(body: any) {
        DocumentService.Add(projectKey, body)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    Fn.PrintMsgSuccessV2(rs);
                    loadData();
                }
            })
    }

    function edit(body: any) {
        DocumentService.GetDetail(projectKey, keyEdit)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    body = Fn.GetTimestampV2(rs, body);
                    DocumentService.Edit(projectKey, keyEdit, body)
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccessV2(rs);
                                loadData();
                            } else setIsLoading(false);
                        })
                } else setIsLoading(false);
            })
    }

    function deleteData() {
        setIsLoading(true);
        DocumentService.GetDetail(projectKey, keyEdit)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    DocumentService.Delete(projectKey, keyEdit, Fn.GetTimestampV2(rs))
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccessV2(rs);
                                loadData();
                                onClose();
                            } else setIsLoading(false);
                        })
                } else setIsLoading(false);
            })
    }

    function handleClickDelete() {
        Swal.fire("Xác nhận xóa?", "Bạn có muốn xóa dữ liệu?", "question")
            .then((isConfirmed) => {
                if (isConfirmed.isConfirmed) {
                    deleteData();
                }
            });
    }

    return (
        <Drawer
            title={keyEdit !== undefined ? "Chỉnh sửa" : "Thêm"}
            open={visible}
            onClose={onClose}>
            <Form
                layout="vertical"
                onSubmitCapture={handleSubmitForm}>
                <MyFormItem.Input
                    label="Tên"
                    value={name}
                    setValue={setName} />

                <HeaderComponent
                    data={item}
                    setData={setItem} />

                <MyButton
                    type="success"
                    isLoading={isLoading}
                    htmlType="submit"
                    value={keyEdit !== undefined ? "Chỉnh sửa" : "Thêm"} />

                <MyButton
                    type="danger"
                    onClick={handleClickDelete}
                    isLoading={isLoading}
                    value="Xóa" />
            </Form>
        </Drawer>
    )
}